import React, { useState, useEffect } from 'react';
import { NavLink, Route, Switch } from 'react-router-dom';
import connect from '@vkontakte/vk-connect';

import Landing from './containers/Landing';

const App = () => {
	const [groupId, setGroupId] = useState(null);
	const [id, setId] = useState(null);

	const url = window.location.search;
	const params = new URLSearchParams(url);

	useEffect(() => {
		connect.subscribe(({ detail: { type, data } }) => {
			if (type === 'VKWebAppAddToCommunityResult') {
				setGroupId(data);
			}
		});

		function hasApp() {
			if(params.has('vk_group_id')) {
				setId(params.get('vk_group_id'));
			}
		}
		hasApp();
	}, [])

	async function connectToGroup() {
		await connect.sendPromise('VKWebAppAddToCommunity');
	}

	const Home = () => <div>Home</div>;
	const Pay = () => <div>Pay</div>;
	const Help = () => <div>Help</div>;

	console.log("groupId", groupId);
	console.log("params", params.get('vk_group_id'));

	return (
		<>
			{!id ? (
				<>
					<Landing handleConnect={connectToGroup} />
				</>
			) : (
				<>
					<header>
						<ul>
							<li><NavLink to="/" exact>Home</NavLink></li>
							<li><NavLink to="/pay" exact>Pay</NavLink></li>
							<li><NavLink to="/help" exact>Help</NavLink></li>
						</ul>
					</header>
					<main>
						<Switch>
							<Route path="/" exact component={Home} />
							<Route path="/pay" component={Pay} />
							<Route path="/help" component={Help} />
						</Switch>
					</main>
				</>
			)}
		</>
	);
}

export default App;

